.loading-box{
  height: 75px;
  width: 75px;
  border: 10px solid white;
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
  -webkit-animation: roll 2s infinite ease-out;
          animation: roll 2s infinite ease-out;
}
.loading-inner-box{
  background: white;
  height: 20px;
  -webkit-animation: fill 2s infinite ease-in;
          animation: fill 2s infinite ease-in;
}

@-webkit-keyframes fill{
  0%{
    height: 0px;
  }
  40%{
    height: 75px;
  }
  60%{
    height: 75px;
  }
  100%{
    height: 0px;
  }
}

@keyframes fill{
  0%{
    height: 0px;
  }
  40%{
    height: 75px;
  }
  60%{
    height: 75px;
  }
  100%{
    height: 0px;
  }
}

@-webkit-keyframes roll{
  0%{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  40%{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  60%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes roll{
  0%{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  40%{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  60%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



