.bubbleProgressIndicator {
  border-radius: 50%;
  height: 120px;
  overflow: hidden;
  width: 120px;
}

.progressIndicatorPlaying {
  -webkit-animation: containerAnimation 3s infinite forwards;
          animation: containerAnimation 3s infinite forwards;
}

@-webkit-keyframes containerAnimation {
  30% {
    background-color: rgba(0, 196, 204, 0);
  }
  45% {
    background-color: rgba(255, 255, 255, 1);
  }
  55% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  65% {
    opacity: 0;
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
  }
  100% {
    background-color: rgba(255, 255, 255, 1);
    opacity: 0;
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
  }
}

@keyframes containerAnimation {
  30% {
    background-color: rgba(0, 196, 204, 0);
  }
  45% {
    background-color: rgba(255, 255, 255, 1);
  }
  55% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  65% {
    opacity: 0;
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
  }
  100% {
    background-color: rgba(255, 255, 255, 1);
    opacity: 0;
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
  }
}

.bubbleProgressIndicator > span {
  background-color: #fff;
  border-radius: 50%;
  height: 20%;
  opacity: 0;
  position: absolute;
  width: 20%;
}

.progressIndicatorPlaying > span {
  -webkit-animation: bubbleAnimation 3s infinite forwards;
          animation: bubbleAnimation 3s infinite forwards;
}

@-webkit-keyframes bubbleAnimation {
  0% {
    opacity: 1;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  13% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  51% {
    opacity: 1;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}

@keyframes bubbleAnimation {
  0% {
    opacity: 1;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  13% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  51% {
    opacity: 1;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}

/* Dimensions */
.bubbleProgressIndicator > span:nth-child(3n) {
  height: 30%;
  width: 30%;
}

.bubbleProgressIndicator > span:nth-child(3n + 1) {
  height: 40%;
  width: 40%;
}

/* Delay and position */
.bubbleProgressIndicator > span:nth-child(1) {
  left: 10%;
  top: 10%;
}

.bubbleProgressIndicator > span:nth-child(2) {
  -webkit-animation-delay: 0.04s;
          animation-delay: 0.04s;
  left: 20%;
  top: 20%;
}

.bubbleProgressIndicator > span:nth-child(3) {
  -webkit-animation-delay: 0.08s;
          animation-delay: 0.08s;
  left: 40%;
  top: 20%;
}

.bubbleProgressIndicator > span:nth-child(4) {
  -webkit-animation-delay: 0.12s;
          animation-delay: 0.12s;
  left: 20%;
  top: 50%;
}

.bubbleProgressIndicator > span:nth-child(5) {
  -webkit-animation-delay: 0.16s;
          animation-delay: 0.16s;
  left: 65%;
  top: 15%;
}

.bubbleProgressIndicator > span:nth-child(6) {
  -webkit-animation-delay: 0.20s;
          animation-delay: 0.20s;
  left: 5%;
  top: 2%;
}

.bubbleProgressIndicator > span:nth-child(7) {
  -webkit-animation-delay: 0.24s;
          animation-delay: 0.24s;
  left: 60%;
  top: 60%;
}

.bubbleProgressIndicator > span:nth-child(8) {
  -webkit-animation-delay: 0.28s;
          animation-delay: 0.28s;
  left: 80%;
  top: 30%;
}

.bubbleProgressIndicator > span:nth-child(9) {
  -webkit-animation-delay: 0.32s;
          animation-delay: 0.32s;
  left: 50%;
  top: 45%;
}

.bubbleProgressIndicator > span:nth-child(10) {
  -webkit-animation-delay: 0.36s;
          animation-delay: 0.36s;
  left: 30%;
  top: -10%;
}

.bubbleProgressIndicator > span:nth-child(11) {
  -webkit-animation-delay: 0.40s;
          animation-delay: 0.40s;
  left: 0%;
  top: 50%;
}

.bubbleProgressIndicator > span:nth-child(12) {
  -webkit-animation-delay: 0.44s;
          animation-delay: 0.44s;
  left: 10%;
  top: 30%;
}

.bubbleProgressIndicator > span:nth-child(13) {
  -webkit-animation-delay: 0.48s;
          animation-delay: 0.48s;
  left: 60%;
  top: 30%;
}
.bubbleProgressIndicator > span:nth-child(14) {
  -webkit-animation-delay: 0.52s;
          animation-delay: 0.52s;
  left: 80%;
  top: 15%;
}

.bubbleProgressIndicator > span:nth-child(15) {
  -webkit-animation-delay: 0.56s;
          animation-delay: 0.56s;
  left: 0%;
  top: 20%;
}

.bubbleProgressIndicator > span:nth-child(16) {
  -webkit-animation-delay: 0.60s;
          animation-delay: 0.60s;
  left: 10%;
  top: 70%;
}

.bubbleProgressIndicator > span:nth-child(17) {
  -webkit-animation-delay: 0.64s;
          animation-delay: 0.64s;
