}

.bubbleProgressIndicator > span:nth-child(18) {
  -webkit-animation-delay: 0.68s;
          animation-delay: 0.68s;
  left: 30%;
  top: 30%;
}

.bubbleProgressIndicator > span:nth-child(19) {
  -webkit-animation-delay: 0.72s;
          animation-delay: 0.72s;
  left: 40%;
  top: 70%;
}

.bubbleProgressIndicator > span:nth-child(20) {
  -webkit-animation-delay: 0.76s;
          animation-delay: 0.76s;
  left: 83%;
  top: 52%;
}

.bubbleProgressIndicator > span:nth-child(21) {
  -webkit-animation-delay: 0.80s;
          animation-delay: 0.80s;
  left: 0%;
