.portholeContainer {
  height: 120px;
  width: 120px;
}

.porthole {
  background-color: #4e535c;
  border-radius: 50%;
  height: 100%;
  -webkit-mask-image: url('data:image/svg+xml;utf8,<svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 100 100" xml:space="preserve"><circle fill="#FFFFFF" cx="50" cy="50" r="50"/></svg>');
  overflow: hidden;
  position: relative;
  -webkit-transition: -webkit-transform 0.3s;
  transition: -webkit-transform 0.3s;
  -o-transition: transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  width: 100%;
  z-index: -1;
}

.portholeFill {
  background-color: #00c4cc;
  position: absolute;
  bottom: -100%;
  height: 100%;
  width: 100%;
  -webkit-animation: moveUp 20s linear infinite;
          animation: moveUp 20s linear infinite;
}

.portholeFill:before {
  -webkit-animation: moveRight 5s linear infinite;
          animation: moveRight 5s linear infinite;
  background: url(data:image/svg+xml;base64,) 0 100% / 75px 46px repeat-x;
  content: '';
  height: 100%;
  margin-top: 1px;
  position: absolute;
  right: 0;
  top: -100%;
  width: 500%;
}

@-webkit-keyframes moveRight {
  0% {
    -webkit-transform: none;
            transform: none;
  }
  100% {
    -webkit-transform: translateX(60%);
            transform: translateX(60%);
  }
}

@keyframes moveRight {
  0% {
    -webkit-transform: none;
            transform: none;
  }
  100% {
    -webkit-transform: translateX(60%);
            transform: translateX(60%);
  }
}

@-webkit-keyframes moveUp {
  0% {
    bottom: -100%;
  }
  100% {
    bottom: 0%;
  }
}

@keyframes moveUp {
  0% {
    bottom: -100%;
  }
  100% {
    bottom: 0%;
  }
}

.portholeFill:after {
  background: url(data:image/svg+xml;base64,) 0 0 / contain no-repeat;
  content: '';
  display: none;
  height: 20px;
  left: -20px;
  position: absolute;
  top: -10px;
  width: 20px;
}

.portholeRubberDuckyVisible > .portholeFill:after {
  -webkit-animation: rubberDuckyFloating 10s linear infinite;
          animation: rubberDuckyFloating 10s linear infinite;
  display: block;
}

@-webkit-keyframes rubberDuckyFloating {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  40% {
    -webkit-transform: translate(0, -5px);
            transform: translate(0, -5px);
  }
  50% {
    -webkit-transform: translate(26px, 0px);
            transform: translate(26px, 0px);
  }
  60% {
    -webkit-transform: translate(46px, -3px);
            transform: translate(46px, -3px);
  }
  70% {
    -webkit-transform: translate(66px, 0px);
            transform: translate(66px, 0px);
  }
  80% {
    -webkit-transform: translate(86px, -3px);
            transform: translate(86px, -3px);
  }
  90% {
    -webkit-transform: translate(106px, 0px);
            transform: translate(106px, 0px);
  }
  100% {
    -webkit-transform: translate(136px, -3px);
            transform: translate(136px, -3px);
  }
}

@keyframes rubberDuckyFloating {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  40% {
    -webkit-transform: translate(0, -5px);
            transform: translate(0, -5px);
  }
  50% {
    -webkit-transform: translate(26px, 0px);
            transform: translate(26px, 0px);
  }
  60% {
    -webkit-transform: translate(46px, -3px);
            transform: translate(46px, -3px);
  }
  70% {
    -webkit-transform: translate(66px, 0px);
            transform: translate(66px, 0px);
  }
  80% {
    -webkit-transform: translate(86px, -3px);
            transform: translate(86px, -3px);
  }
  90% {
    -webkit-transform: translate(106px, 0px);
            transform: translate(106px, 0px);
  }
  100% {
    -webkit-transform: translate(136px, -3px);
            transform: translate(136px, -3px);
  }
}



